@import "./../../scssVariables";
.card{
  font-size: 300px;
}


.cardImageWrapper{
  padding: 10px;
  text-align: center;
}
.cardImage{
  height: 170px;
  max-width: 200px;
  width: 100%;
  margin: 0 auto;

  object-fit: cover;
  background-color: $gray;
}
.cardContent{
  max-height: 166px;
  overflow: auto;
  background-color: $gray;
}
.buttonRefresh{
  position: absolute;
  right: 25px;
  bottom: 25px;
}